import React, { Fragment } from "react";
// eslint-disable-next-line no-unused-vars
import { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getDogs } from "../actions";
import { Link } from "react-router-dom";
import Card from "./Card";
import Paginado from "./Paginado";

export default function Home() {
  const dispatch = useDispatch();
  const allDogs = useSelector((state) => state.dogs);
  //paginado
  const [currentPage, setCurrentPage] = useState(1);
  // eslint-disable-next-line no-unused-vars
  const [dogsPP, setDogPP] = useState(8);
  const indexOfLastDog = currentPage * dogsPP; //1*8
  const indexOfFirstDog = indexOfLastDog - dogsPP;
  const currentDogs = allDogs.slice(indexOfFirstDog, indexOfLastDog);

  const paginado = (PageNumber) => {
    setCurrentPage(PageNumber);
  };

  useEffect(() => {
    dispatch(getDogs());
  }, [dispatch]);

  //para q no se recargue innecesariamente
  function handleClick(e) {
    e.preventDefault();
    dispatch(getDogs());
  }

  return (
    <div>
      <Link to="/Dogs">Create Dog</Link>
      <h1>aaaaaaa</h1>
      <button
        onClick={(e) => {
          handleClick(e);
        }}
      >
        recargar dogs
      </button>
      <div>
        <select>
          <option value="weightAsc">weight asc</option>
          <option value="weightDes">weight des</option>
        </select>
        <select>
          <option value="alfAsc">alfabetico asc</option>
          <option value="alfDes">alfabetico des</option>
        </select>
        <select>
          <option value="apiBreed">ApiBreed</option>
          <option value="createBreed">CreatedBreed</option>
          <option value="all">all</option>
        </select>
        <Paginado
          dogsPP={dogsPP}
          allDogs={allDogs.lenght}
          paginado={paginado}
        />
        {currentDogs &&
          currentDogs.map((c) => {
            return (
              <Fragment>
                <Link to={"/home/" + c.temperament}>
                  <Card
                    name={c.name}
                    image={c.image}
                    temperament={c.temperament}
                    weight={c.weight}
                  />
                </Link>
              </Fragment>
            );
          })}
      </div>
    </div>
  );
}
