import React from "react";

export default function Paginado(dogsPP, allDogs, paginado) {
  const PageNumber = [];
  for (let i = 0; i <= Math.ceil(allDogs / dogsPP); i++) {
    PageNumber.push(i + 1);
  }
  return (
    <nav>
      <ul>
        {PageNumber &&
          PageNumber.map((number) => {
            return (
              <li key={number}>
                <h5 onClick={() => paginado(number)}>{number}</h5>
              </li>
            );
          })}
      </ul>
    </nav>
  );
}
